#agcfw_admin_app #license-placeholder {
  margin-top: 2em;
  border-top: 1px solid #f3f3f3;
  padding-top: 2em;

  .license-info {
    position: relative;

    .heading {
        
      .left {
        width: calc(100% - 192px) !important;
      }
    }

    .overlay {
      display: none;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(255, 255, 255, 0.6);
      align-items: center;
      justify-content: center;
      text-align: center;
    }

    &.show .overlay {
      display: flex;
    }

    .active-indicator.activated {
      background: #c6cd2e;
      padding: 12px 16px;

      .anticon {
        margin-right: 0.5em;
      }
    }
  }

  .no-hover {
    opacity: 1 !important;
  }

  .license-form {
    padding: 20px 30px;
    border-top: 1px solid #d5d5d5;
    background: #f1f1f1;

    .ant-form {
      display: flex;
      align-items: end;
      justify-content: space-between;
      
      .ant-row {
        width: calc(50% - 99px);
        margin-bottom: 10px;

          &.form-action {
            width: 100%;
            max-width: 198px;
          }
      }

      .ant-form-item-label {
        padding-bottom: 5px;
      }

      input, button {
        border-radius: 2px;
        font-size: 16px;
      }

      label {
        display: inline-block;
        font-size: 13px;
        color: #444;
        font-weight: bold;
      }

      input {
        padding: 5px 10px;
        width: calc(100% - 15px);
        border: 1px solid #D9D9D9;
        box-sizing: border-box;    
      }

      button {
        width: 100%;
        margin-top: 27px;
        background: #C6CD2E;
        cursor: pointer;
        border: 0;
        color: #262626;
        height: 43px;
        font-weight: bold;

        &:hover {
          opacity: 0.8;
        }
      }

      .grayed button {
        background: #ccc;
      }
    }

    .help-row {
      font-size: 13px;

      a {
        color: inherit;
        text-decoration: underline;

        &:hover {
          color:#00a0d2;
        }
      }
    }
  }
}